export default {
	data() {
		return {
			account:'',
			pas:'',
			checked:'',
			disabled:'true',
			miyao:'',
			kcnueg: true
		}
	},
	methods: {
		onaccount: function(event) {
			this.account = event.target.value
		},
		onpas: function(event) {
			this.pas = event.target.value
		},
		checkBox: function(a) { //是否禁用
			console.log(a)
			this.checked == a
			if (this.checked == "true") {
				this.disabled = false

			} else {
				this.disabled = true
			}


		},
		bianji(){//编辑按钮
			if(this.kcnueg == false){
				this.kcnueg = true
			}else{
				this.kcnueg = false
			}
		},
		btn: function() {
			let this_ =this
			if (this.account.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入用户名'
				});
				return;
			}
			if (this.pas.length <= 0) {
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return;
			}
			uni.request({
				url: 'http://ceshi3.dishait.cn/api/login', //仅为示例，并非真实接口地址。
				data: {
					username:this_.account,//user2
					password:this_.pas//zcmcss
				},
				method: 'POST',
				success: (res) => {
					this_.miyao = res.data
					uni.setStorage({
					key:"miyao",
					data:res.data
					});
					console.log(this_.miyao)
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});
					if (res.data.msg == "ok") {
						uni.showToast({
						icon: 'none',
						title: '登录成功'
					});
						return;
					
					};
					
					
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				},
			})

		}

	},
	created() { //数据请求
		uni.getStorage({
		key:"miyao",
		success(e){
			console.log(e.data)
		e.data//这就是你想要取的token
		}
		});
	},



}
